<template>
    <button >
         <p>{{Residents_Names}} ({{Residents_ID}})</p>
    </button>
</template>

<script>
/* eslint-disable */
import api from '../../api'
export default{
    name: 'Exibir_Residents',
    data() {
        return{
            Residents_Names: null,
            Residents_ID: null,
        }
        
    },
    props: {
      testi: String,
      name: String,
    },
    methods: {
        testando2() {           
            api.get(`${this.testi}`)
                .then((response) => {
                this.Residents_Names = response.data.name;
                this.Residents_ID = response.data.id
                
            });
        },
        
    },
    mounted(){
        this.testando2();
    },
    
}
</script>

