<template>
  <main>
    <section class="area">
      <!--Entrada de info-->
      <div class="area_Procurar"> 
        <input
        class="area_input" 
        type="text" 
        v-model="Digitos"
        @keyup.enter="Digicted"
        />

        <input 
        type="submit"
        class="area_button" 
        value="Concluir"
        @click="Digicted"
        />

        <!--Exibir Info-->
        <div class="area_info" v-show="Show">
          <h1>Nome: <span>{{name}}</span></h1>
          <img :src="image" />

          <p>Origem: <span>{{location}}</span></p>
          <p>Genero: <span>{{gender}}</span></p>
          <p>Status: <span>{{status}}</span></p>
          <p>Specie: <span>{{specie}}</span></p>
          <button @click="Testando()">Residentes</button>
        </div>

        <!--Consultar Nomes-->
        <div class="area_total_consulta">
          <div v-for="(nome, index) in nomes" :key="index" class="area_consulta" v-show="Hide">
            <button @click="Teste(nome.id)">{{nome.name}} ({{nome.id}})</button>
          </div>
        </div>
      </div>
      <!--Consultar Residentes-->
      <section class="area_residents" v-show="Show_residents">
        <div class="area_residents_button">
          <button @click="hide_residents">X</button>
        </div>
        <h5 class="area_residents_titulo">Residents / Moradores <span>/ Nomes(ID)</span></h5>
        <h5 class="area_residents_titulo">{{name}}</h5>
        
        <div class="area_total_consulta">
          <div class="area_consulta" >
            <Exibir_Residents v-for="(indlink,index) in nomes" :key="index" :testi="indlink" :name="name"/>
          </div>
        </div>
      </section>
    </section>
  </main>
</template>

<script>

/* eslint-disable */
import api from '../api'
import Exibir_Residents from '@/components/Home/Exibir_Residents.vue';

export default {
  name: "HelloWorld",
  props: {
      msg: String
  },
  data() {
      return {
        //Variaveis
          Digitos: null,
          Show: null,
          Hide: null,

          name: null,
          image: null,
          location: null,
          gender: null,
          status: null,
          specie: null,
          Residents: null,

          arr: [],

          Resultados_arrays: null,
          nomes: null,
          teste: null,

          Show_residents: null,
      };
  },
  methods: {
    //Pegando ID do nome procurado
      Teste(id) {
          this.Digitos = id;
          this.CharacterNAME();
      },
    //Pegando ID ou Nome digitado
      Digicted() {
          this.arrays_residents = [];
          this.Show_residents = false;
          if (this.Digitos > 0) {
              this.CharacterNAME();
          }
          else {
              this.CharacterID();
          }
      },
      //Tratando se for ID
      CharacterNAME() {
        this.Show = true;
        this.Hide = false;
        this.Show_residents = false;
        api.get(`/character/${this.Digitos}`)
          .then((response) => {
            this.Digitos = null;
            this.nomes = null;
            this.name = response.data.name;
            this.image = response.data.image;
            this.location = response.data.location.name;
            this.gender = response.data.gender;
            this.status = response.data.status;
            this.specie = response.data.species;
            this.Residents = response.data.location.url;
            });
      },
      //Tratando se for Nome
      CharacterID() {
        this.Show_residents = false;
        this.Show = false;
        this.Hide = true;
        api.get(`/character/?name=${this.Digitos}`)
          .then((response) => {
            this.Digitos = null;
            this.nomes = response.data.results;
          });
      },
      //Exibindo Residentes
      Testando() {
        this.Show_residents = true;
        api.get(`${this.Residents}`)
          .then((response) => {
            this.nomes = response.data.residents;
          });
      },
      //Escondendo Residentes
      hide_residents() {
        this.Show_residents = false;
      }
  },
  //Componentes
  components: { 
    Exibir_Residents, 
    Headers 
  }
}
</script>


<style>
/*-----------------------------------------------------------------------


                             Pegando Info


-----------------------------------------------------------------------*/
  .area{
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;
  }

  .area_Procurar{
    width: 35%;
    height: 70%;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    border: 1px solid white;
    border-radius: 5px;

    background-color: rgba(0, 0, 0, 0.475);
    backdrop-filter: blur(10px);
  }

  .area_Procurar input{
    margin-top: 2%;
  }

  .area_input{
    background-color: transparent;
    border: 1px solid white;
    padding: .7em .7em;
    outline: none;

    color: rgb(131, 255, 127);

    font-size: 1.5em;
  }

  .area_button{
    background-color: transparent;
    color: white;
    font-size: 1.1em;
    cursor: pointer;

    border: 1px solid white;
    padding: 3px 10px;
    border-radius: 5px;

    transition: .5s ease;
  }

  .area_button:hover{
    transition: .5s ease;
    transform: scale(1.1);
    color: rgb(131, 255, 127);
    border-color: rgb(162, 251, 159);
  }

  /*-----------------------------------------------------------------------


                             Exibindo Info


-----------------------------------------------------------------------*/

  .area_info{
    width: 100%;
    height: 100%;


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  .area_info h1{
    margin-top: 1.1em;
    color: white;
    font-size: 1.5em;
  }

  .area_info h1 span{
    color: rgb(162, 251, 159);
  }

  .area_info img{
    border: 1px solid rgb(131, 255, 127);
    border-radius: 3px;

    margin-bottom: 1.1em;
  }

  .area_info p{
    color: white;
    font-size: 1.5em;
  }
  .area_info p span{
    color: rgb(162, 251, 159);
  }

  .area_info button{
    padding: 3px 6px;
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1.1em;

    margin-top: 3px;
    transition: .5s ease;
  }

  .area_info button:hover{
    transition: .5s ease;
    color: rgb(131, 255, 127);
    border: 1px solid rgb(131, 255, 127);
  }

/*-----------------------------------------------------------------------


                             Consulta


-----------------------------------------------------------------------*/
  .area_total_consulta{
    width: 100%;
    height: 100%;


    display: grid;
    grid-template-columns: auto auto;
    overflow: auto;
    text-align: center;
  }

  .area_total_consulta button{
    font-size: 1.3em;

    color: white;
    background-color: transparent;
    cursor: pointer;

    transition: .5s ease;

    border: 1px solid rgb(255, 255, 255);
    border-radius: 8px;
    padding: 10px 1px;
  }

  .area_total_consulta button:hover{
    transition: .5s ease;
    color: rgb(131, 255, 127);
  }

/*-----------------------------------------------------------------------


                             Residentes


-----------------------------------------------------------------------*/



  .area_residents{
    margin-left: 3%;  
    border: 1px solid rgb(162, 251, 159);

    width: 30vw;
    height: 60vh;
    overflow: auto;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    z-index: 1;
  }

  .area_residents_button{
    width: 100%;

    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .area_residents_button button{
    border: 1px solid #fff;
    border-radius: 0px 0px 6px 0px;

    cursor: pointer;
    
    background-color: rgba(0, 0, 0, 0.19);

    color: white;

    font-weight: -800;
    transition: .5s ease;
  }

  .area_residents_button:hover{
    transition: .5s ease;
    color: rgb(131, 255, 127);
    border-color: rgb(162, 251, 159);
  }



  .area_residents_titulo{
    color: rgb(131, 255, 127);
    border-bottom: 1px solid white ;
    font-size: 1.1em;
    font-family: sans-serif;
  }

  .area_residents_titulo span{
    color: white;
  }


  .area_consulta{
    display: grid;
    row-gap: 3px;

    padding: 1em 2em;
  }

  /*-----------------------------------------------------------------------


                             @Media Queries


-----------------------------------------------------------------------*/

@media (max-width: 600px) {
    .area_Procurar{
    width: 95%;
    height: 70%
  }
  .area{
    flex-direction: column;
    row-gap: 1%;
  }

    .area_residents{
    width: 95vw;
    height: 60vh;
    position: sticky;
  }

    .area_residents{
    margin-left: 0%;  
  }

  .area_info p{
    font-size: 1.1em;
  }

  .area_info h1{
    color: white;
    font-size: 1.2em;
  }

  .area_info img{
    width: 250px
  }

};

</style>
